
















































































import is from 'is'
import Utils from '../../utils'
import city from '../../utils/city'
import mixin from '../../utils/mixin'
let key = 'ECSBZ-DYVKU-ZMEVX-4UQSY-C35FJ-FJF3G';
export default {
    mixins: [mixin],
    data: {
        searchLetter: [],
        showLetter: "",
        winHeight: 0,
        cityList: [],
        toastShowLetter: '',
        isShowLetter: false,
        scrollTopId: '', //id
        city: "",
        currentCityCode: '',
        hotcityList: [{
            cityCode: 110000,
            city: ''
        }, {
            cityCode: 310000,
            city: ''
        }, {
            cityCode: 440100,
            city: ''
        }, {
            cityCode: 440300,
            city: ''
        }, {
            cityCode: 330100,
            city: ''
        }, {
            cityCode: 320100,
            city: ''
        }, {
            cityCode: 420100,
            city: ''
        }, {
            cityCode: 120000,
            city: ''
        }, {
            cityCode: 610100,
            city: ''
        }, ],
        commonCityList: [{
            cityCode: 110000,
            city: ''
        }, {
            cityCode: 310000,
            city: ''
        }],
        distList: [{
            cityCode: 110000,
            dist: 'A'
        }, {
            cityCode: 310000,
            dist: 'B'
        }, {
            cityCode: 440100,
            dist: 'C'
        }, {
            cityCode: 440300,
            dist: 'D'
        }, {
            cityCode: 330100,
            dist: 'E'
        }, {
            cityCode: 320100,
            dist: 'F'
        }, {
            cityCode: 420100,
            dist: 'G'
        }],
        inputName: '',
        completeList: [],
        dist: '',
        condition: false,
    },
    methods: {
        init() {
            let _this = this;
            const searchLetter = city.searchLetter;
            const cityList = city.cityList();
            const sysInfo = uni.getSystemInfoSync();
            console.log(sysInfo);
            const winHeight = sysInfo.windowHeight;
            const itemH = winHeight / searchLetter.length;
            let tempArr = [];

            searchLetter.map(
                (item, index) => {
                    // console.log(item);
                    // console.log(index);
                    let temp = {};
                    temp.name = item;
                    temp.tHeight = index * itemH;
                    temp.bHeight = (index + 1) * itemH;
                    tempArr.push(temp)
                }
            );
            _this.winHeight = winHeight;
            _this.itemH = itemH;
            _this.searchLetter = tempArr;
            _this.cityList = cityList;
            _this.getLocation(0);
            _this.city = _this.location.city;
            _this.dist = _this.location.dist;
        },
        clickLetter(e) {
            let _this = this;
            console.log(e);
            let showLetter = e.currentTarget.dataset.letter;
            _this.toastShowLetter = showLetter;
            _this.isShowLetter = true;
            _this.scrollTopId = showLetter;
            setTimeout(function() {
                _this.isShowLetter = false;
            }, 500);
        },
        reGetLocation() {
            let _this = this;
            _this.$store.commit('set_location', {
                city: _this.city,
                dist: _this.dist
            });
            //
        },
        //
        bindCity(e) {
            let _this = this;
            _this.condition = true;
            _this.city = e.currentTarget.dataset.city;
            _this.currentCityCode = e.currentTarget.dataset.code;
            _this.scrollTopId = 'top';
            _this.completeList = [];
            _this.selectDist();

            _this.$store.commit('set_location', {
                city: _this.city,
                dist: ''
            });
        },
        bindDist(e) {
            let _this = this;
            console.log(e)
            _this.dist = e.currentTarget.dataset.city;
            _this.$store.commit('set_location', {
                city: _this.city,
                dist: _this.dist
            });
            uni.navigateBack();
        },
        //
        hotCity() {
            this.scrollTopId = 'top';
        },
        bindScroll(e) {
            console.log(e.mp.detail)
        },
        selectDist() {
            let _this = this;
            console.log("");
            let code = _this.currentCityCode;
            uni.request({
                url: `https://apis.map.qq.com/ws/district/v1/getchildren?&id=${code}&key=${key}`,
                success(res) {
                    _this.distList = res.data.result[0];
                    console.log("" + `https://apis.map.qq.com/ws/district/v1/getchildren?&id=${code}&key=${key}`);
                },
                fail() {
                    console.log("");
                }
            })
        },
        getLocation(refresh = 1) {
            let _this = this;
            console.log("");
            _this.dist = '';
            uni.getLocation({
                type: 'wgs84',
                success(res) {
                    let latitude = res.latitude
                    let longitude = res.longitude
                    uni.request({
                        url: `https://apis.map.qq.com/ws/geocoder/v1/?location=${latitude},${longitude}&key=${key}`,
                        success: res => {
                            console.log(res);
                            _this.city = res.data.result.ad_info.city;
                            let nation_code = res.data.result.ad_info.nation_code;
                            let city_code = res.data.result.ad_info.city_code;
                            _this.currentCityCode = city_code.replace(nation_code, '');
                            _this.dist = res.data.result.ad_info.district;
                            if (refresh == 0) {
                                _this.$store.commit('set_location', {
                                    city: _this.city,
                                    dist: _this.dist
                                });
                            }
                            _this.condition = true;
                            _this.selectDist();
                        }
                    })
                }
            });
        },
        bindBlur(e) {
            let _this = this;
            _this.inputName = '';

        },
        bindKeyInput(e) {
            let _this = this;
            _this.inputName = e.mp.detail.value;
            _this.auto();
        },
        auto() {
            let _this = this;
            let inputSd = _this.inputName.trim();
            let sd = inputSd.toLowerCase();
            let num = sd.length;
            const cityList = city.cityObjs;
            // console.log(cityList.length)
            let finalCityList = [];

            let temp = cityList.filter(
                item => {
                    let text = item.short.slice(0, num).toLowerCase()
                    return (text && text == sd)
                }
            );
            //shorter
            let tempShorter = cityList.filter(
                itemShorter => {
                    if (itemShorter.shorter) {
                        let textShorter = itemShorter.shorter.slice(0, num).toLowerCase()
                        return (textShorter && textShorter == sd)
                    }
                    return
                }
            );

            let tempChinese = cityList.filter(
                itemChinese => {
                    let textChinese = itemChinese.city.slice(0, num)
                    return (textChinese && textChinese == sd)
                }
            );

            if (temp[0]) {
                temp.map(
                    item => {
                        let testObj = {};
                        testObj.city = item.city
                        testObj.code = item.code
                        finalCityList.push(testObj)
                    }
                );
                _this.completeList = finalCityList;
            } else if (tempShorter[0]) {
                tempShorter.map(
                    item => {
                        let testObj = {};
                        testObj.city = item.city
                        testObj.code = item.code
                        finalCityList.push(testObj)
                    }
                );
                _this.completeList = finalCityList;
            } else if (tempChinese[0]) {
                tempChinese.map(
                    item => {
                        let testObj = {};
                        testObj.city = item.city
                        testObj.code = item.code
                        finalCityList.push(testObj)
                    });
                _this.completeList = finalCityList;
            } else {
                return
            }
        }
    },
    async onLoad() {
        let _this = this;
        console.log('onLoad');
        if (!_this.shouldLogin) {
            console.log('init');
            _this.init();
        }
    },
}
